import { useState, useEffect } from "react"
import { ChevronLeft, ChevronRight } from "react-feather"

export default function Carousel({children: slides, autoSlide = false, autoSlideInterval = 3000}) {

  const [curr, setCurr] = useState(0)

  const prev = () =>
    setCurr((curr) => (curr === 0 ? slides.length - 1 : curr - 1))
  const next = () =>
    setCurr((curr) => (curr === slides.length - 1 ? 0 : curr + 1))


  useEffect(() => {
    if (!autoSlide) return
    const slideInterval = setInterval(next, autoSlideInterval)
    return () => clearInterval(slideInterval)
  }, [])


  return (
    <div className="overflow-hidden relative mt-5 mx-5 sm:h-[10rem] md:h-[25rem] lg:h-[30rem]">

        {/* images */}
        <div className="flex h-full w-full transition-transform ease-out duration-500" style={{ transform: `translateX(-${curr * 100}%)` }} >
            {slides}
        </div>


        {/* prev and next buttons */}
        <div className="absolute inset-0 flex items-center justify-between p-4">

            <button onClick={prev} className="p-1 rounded-full shadow bg-white/80 text-gray-800 hover:bg-white">
                <ChevronLeft size={40} />
            </button>

            <button onClick={next} className="p-1 rounded-full shadow bg-white/80 text-gray-800 hover:bg-white">
                <ChevronRight size={40} />
            </button>

        </div>


        {/* dots */}
        <div className="absolute bottom-4 right-0 left-0">

            <div className="flex items-center justify-center gap-2">
            {slides.map((indicator, index) => (
                <div className={`
                    transition-all w-3 h-3 bg-slate-950 rounded-full
                    ${curr === index ? "p-2" : "bg-opacity-50"}
                `}/>
            ))}
            </div>

        </div>
    </div>
  )
}